import React from 'react';
import { NavLink } from 'react-router-dom';

const Home = () => {
    return (
        <>
            <section id="header">
                <div className="max-width">
                    <div className="brand-name">
                        Khan
                    </div>
                    <div className="intro">
                        <h1>Simplicity in every web design.</h1>
                    </div>
                </div>
                <div className="photo"></div>
                <div className="max-width content">
                    <p className="large">Hi, I’m Khan. I'm a multi-talented human with over 10+ years of experiences in wide range of design disciplines.</p>
                    <p className="small">You can also call me a product designer, experience designer, interaction, UI, UX or by any other market defined function-title. I prefer to keep learning, continue challenging myself, and do interesting things that matter. My abundant energy fuels me in the pursuit of many interests, hobbies, areas of study and artistic endeavors. I’m a fast learner, able to pick up new skills and juggle different projects and roles with relative ease.</p>
                    <p className="small">I like to develop expertise in a number of areas over the course of my life and career. I currently work remotely and have a full-time commitment at <NavLink to="http://invisionapp.com" target="_blank" rel="noopener noreferrer">InVision</NavLink>. Outside of my commitments I work with a select freelance client base. Feel free to get in touch by any means of communication.</p>
                    <div className="contact-list">
                        <div className="contact">
                            xyz@gmail.com
                            </div>
                            <div className="contact">
                            <a  href="https://www.facebook.com" target="_blank" rel="noopener noreferrer">Facebook</a>
                            </div>
                            <div className="contact">
                            <a  href="https://twitter.com/" target="_blank" rel="noopener noreferrer">Twitter</a>
                            </div>
                            <div className="contact">
                            <a  href="https://www.dribbble.com" target="_blank" rel="noopener noreferrer">Dribbble</a>
                            </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;